<template>
  <div>
    <LayoutBox one="小超店铺管理">
      <h4>小超店铺管理</h4>
      <router-link to="/admin/addAdd"><el-button class="inquire" type="success" icon="el-icon-plus">新增店铺</el-button></router-link>
      <el-table :data="shopList" height="500">
        <el-table-column prop="shopName" label="商铺名称"></el-table-column>
        <el-table-column prop="name" label="联系人"></el-table-column>
        <el-table-column prop="mobile" label="账号"></el-table-column>
        <el-table-column prop="area" label="地区"></el-table-column>
        <el-table-column prop="adderss" label="详细地址"></el-table-column>
        <el-table-column prop="tax" label="税号"></el-table-column>
        <el-table-column label="状态">
          <template slot-scope="scope">
            <span>{{ scope.row.approvalStatus === 1 ? '已通过' : scope.row.approvalStatus === 2 ? '不通过' : '待审核' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button type="text" @click.native.prevent="onLook(scope)">查看</el-button>
            <el-button type="text" @click.native.prevent="onDelete(scope)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页 -->
      <el-pagination background layout="prev, pager, next" :total="totle" @current-change="handleCurrentChange"></el-pagination>
    </LayoutBox>
  </div>
</template>

<script>
import LayoutBox from '@/components/LayoutBox/index'

export default {
  name: 'SuperMarketAddList',

  components: {
    LayoutBox
  },

  data () {
    return {
      totle: 0, // 分页总条数
      pageNo: 1, // 分页当前页
      shopList: [] // 商铺列表
    }
  },

  created () {
    this._getShopList()
  },

  methods: {
    // 分页
    handleCurrentChange (pageNo) {
      this.pageNo = pageNo
      this._getShopList()
    },
    // 删除
    onDelete(scope) {
      this.$confirm('此操作将永久删除商铺, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this._deleteShop(scope.row.id)
          .then((res) => {
            if (res) {
              this.$message.success('删除成功')
              this.shopList.splice(scope.$index, 1)
            }
          })
      })
    },
    // 查看
    onLook (scope) {
      this.$router.push({ path: '/admin/addDetail', query: { id: scope.row.id }})
    },

    // API 接口--------------------------------------------------
    // 获取列表
    _getShopList () {
      const DATA = {
        pageNo: this.pageNo,
        shopType: 2
      }
      this.$http.post('/woho/shop/manage/query', DATA)
        .then((res) => {
          this.shopList = res.data.data.list
          this.totle = res.data.data.total
        })
    },
    // 删除商铺
    _deleteShop (id) {
      return this.$http.delete('/woho/shop/manage/delete/' + id)
        .then((res) => {
          return res.data.success
        })
    }
    // API 接口--------------------------------------------------
  }
}
</script>

<style lang="scss" scoped>

</style>
